.service {
  background-color: #f2f6fa;
  padding: 120px 0;

  @include onTablet {
    padding: 148px 0;
  }

  @include onDesktop {
    padding-top: 287px;
  }

  // @include onTablet{
  //   display: grid;
  //   grid-template-columns: repeat(6, 1fr);
  // }

  &__wrapper {
    @include onDesktop {
      display: flex;
      justify-content: space-between;
    }
  }

  &__header {
    @include onTablet {
      max-width: 263px;
    }
  }

  &__label {
    margin: 0;
    margin-bottom: 16px;
    color: #c0cdd7;
    font-weight: 700;
    font-size: 13px;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    color: #253757;
    font-weight: 600;
    font-size: 32px;
    line-height: 41.6px;
  }

  &__text {
    font-family:
      Open Sans,
      serif;
    margin: 0;
    margin-bottom: 48px;
    color: #6c788b;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
  }

  &__cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;

    @include onTablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
    }

    @include onDesktop {
      width: 60%;
    }
  }

  &__card {
    box-sizing: border-box;
    border: 1px solid #fff;
    background-color: #fff;
    border-radius: 16px;
    width: 100%;
    height: auto;

    transition: all 0.3s ease;

    &:hover {
      border: 1px solid rgba(17, 38, 144, 0.5);
    }

    &--top {
      @include onTablet {
        transform: translateY(-50%);
      }
    }

    &-content {
      padding: 32px;
    }

    &-number {
      display: inline-block;
      color: #2060f6;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
      padding: 2px 12px;
      border-radius: 23px;
      background-color: #f2f6fa;
    }

    &-desc {
      margin: 0;
      margin-bottom: 32px;
      margin-top: 24px;
      font-family:
        Open Sans,
        serif;
      color: #6c788b;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
    }

    &-title {
      position: relative;
      cursor: pointer;
      text-decoration: none;
      color: #334563;
      padding-left: 56px;
      font-weight: 600;
      font-size: 15px;
      line-height: 22.5px;

      &::before {
        position: absolute;
        left: 0;
        bottom: -8px;
        height: 40px;
        width: 40px;
        content: url('./../images/service/Arrow.svg');
      }
    }
  }
}
