.testimonial {
  padding-bottom: 120px;

  @include onTablet {
    padding-bottom: 148px;
  }

  &__label {
    margin: 0;
    margin-bottom: 16px;
    color: #c0cdd7;
    font-weight: 700;
    font-size: 13px;
    line-height: 19.5px;
    letter-spacing: 3px;
    text-transform: uppercase;
  }

  &__title {
    margin: 0;
    margin-bottom: 48px;
    color: #253757;
    font-weight: 600;
    font-size: 32px;
    line-height: 48px;

    @include onTablet {
      max-width: 200px;
      margin-bottom: 56px;
    }

    @include onDesktop {
      margin-bottom: 85px;
    }
  }

  &__cards {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;

    @include onTablet {
      gap: 30px;
    }

    @include onDesktop {
      flex-direction: row;
    }
  }

  &__card {
    background-color: #fff;
    border-radius: 16px;

    transition: all 0.3s ease;

    &:hover {
      transform: scale(1.05);
      box-shadow: 5px 5px 10px rgba(48, 80, 222, 0.5);
    }

    @include onDesktop {
      width: 362px;
    }

    &-content {
      padding: 32px 42px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
    }

    &-img {
      width: 100%;
      max-width: 250px;
      height: 100%;
      object-fit: cover;
    }

    &-mark {
      width: 24px;
      height: 24px;
      margin-bottom: 8px;

      @include onTablet {
        margin-bottom: 16px;
      }
    }

    &-info {
      margin: 0;
      margin-bottom: 16px;
      font-family:
        Open Sans,
        serif;
      color: #6c788b;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
    }

    &-name {
      margin: 0;
      margin-bottom: 4px;
      font-family:
        Open Sans,
        serif;
      font-weight: 600;
      font-size: 13px;
      line-height: 19.5px;
      color: #253757;
    }

    &-desc {
      margin: 0;
      color: #c0cdd7;
      font-weight: 700;
      font-size: 13px;
      line-height: 19.5px;
      letter-spacing: 3px;
      text-transform: uppercase;
    }
  }
}
